export var dpr = window.devicePixelRatio >= 3 ? 3 : 2;
//export default const str = 'hello world'

export var imgLoad = function(url, callback) {
  var img = new Image();
  img.src = url;
  if (img.complete) {
    callback(img.width, img.height);
  } else {
    img.onload = function() {
      callback(img.width, img.height);
      img.onload = null;
    };
  }
};

